import Vue from 'vue'
import Router from 'vue-router'
import home from '@/page/home'
import cate from '@/page/cate'
import cate_goods from '@/page/cate_goods'
import shop from '@/page/shop/shop'
import weixin from '@/page/shop/weixin'
import map from '@/page/shop/map'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            component: home
        },
        {
            path: '/cate',
            redirect: '/cate/1'
        },
        {
            path: '/cate/:id',
            component: cate,
            children: [
                {
                    path: 'list',
                    component: cate_goods
                }
            ]
        },
        {
            path: '/shop',
            component: shop,
            children:[
                {
                    path: 'map',
                    component: map
                },
                {
                    path: 'weixin',
                    component: weixin
                }
            ]
        }
    ]
})


