<template>
  <div class="page-sub">
    <qs-header goBack="true" title="丝薄系列"></qs-header>
    <section class="page-sub-body" ref="wrapper">
      <div>
        <p>{{ $route.params.id }}</p>
      </div>
    </section>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import qsHeader from '../components/header'
export default {
  name: 'cate_goods',
  data () {
    return {
      
    }
  },
  components: {
    qsHeader
  },
  mounted () {
    setTimeout(() => {
      new BScroll(this.$refs.wrapper, {}); 
    }, 20);
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/mixin';

</style>



