<template>
  <div class="page-sub">
    <qs-header goBack="true" title="扫码加微信"></qs-header>
    <section class="page-sub-body" ref="wrapper">
      <div>
        <p class="text-center pdt-30"><img src="../../assets/images/wx-ma.png" alt=""></p>
        <div class="text-left pdl-50 pdr-50 mgt-10">
          <p class="">方式1：微信浏览时，长按上面图片，选择“识别图中二维码”加微信好友</p>
          <p class="mgt-10">方式2：其他工具浏览时，长按上面图片，保存图片到手机相册，然后微信扫一扫，选择相册图片加微信好友</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import qsHeader from '../../components/header'
export default {
  name: 'weixin',
  data () {
    return {
      
    }
  },
  components: {
    qsHeader
  },
  mounted () {
    setTimeout(() => {
      new BScroll(this.$refs.wrapper, {}); 
    }, 20);
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';

</style>



