<template>
  <nav class="qs-nav">
    <li>
      <router-link to="/home">
        <svg class="icon" aria-hidden="true"><use xlink:href="#qsi-home"></use></svg>
        <span class="nav-text">首页</span>
      </router-link>
    </li>
    <li>
      <router-link to="/cate">
        <svg class="icon" aria-hidden="true"><use xlink:href="#qsi-appstore"></use></svg>
        <span class="nav-text">分类</span>
      </router-link>
    </li>
    <li>
      <router-link to="/shop">
        <svg class="icon" aria-hidden="true"><use xlink:href="#qsi-shopping"></use></svg>
        <span class="nav-text">店铺</span> 
      </router-link>
    </li>
  </nav>
</template>

<script>
export default {
  data () {
    return {
      
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../assets/css/mixin';
  .qs-nav{
    position: fixed;
    left: 0;
    bottom: 0;
    @include flex;
    width: 100%;
    border-top: 1px solid #e6e4e4;
    background-color: #fff;
    z-index: 999;
    > li{
      @include flex-1;
      height: rem(93);
      text-align: center;
      >a{
        display: inline-block;
        height: rem(93);
        padding-top: rem(12);
        font-size: rem(36);
        &.router-link-active{
          color: #1c78b7;
          .nav-text{
            color: #1c78b7;
          }
        }
      }
    }
    .nav-text{
      display: block;
      font-size: rem(24);
    }
  }
</style>



