<template>
  <div>
    <div class="shop-head pdl-30 pdr-30">
      <p class="img"></p>
      <div class="text mgl-20">
        <h2 class="fs-32 color-white">凯儿得樂青神专营店欢迎您</h2>
        <p class="mgt-30 fs-24 color-white text-right">关注健康 得乐成长</p>
      </div>
    </div>

    <div class="shop-notice pdl-30 fz-22">
      <svg class="icon" aria-hidden="true"><use xlink:href="#qsi-sound"></use></svg>
      <span class="fz-20">凯儿得樂青神专营店官网将于2018年12月1日正式上线</span>
    </div>

    <dl class="shop-dl" @click="goMap">
      <dt>地址</dt>
      <dd>眉山市青神县建设路34号(实验小学旁)<svg class="icon mgl-10" aria-hidden="true"><use xlink:href="#qsi-right"></use></svg></dd>
    </dl>
    <a href="tel:18382069823">
      <dl class="shop-dl">
        <dt>电话</dt>
        <dd>18382069823(帅女士) <svg class="icon mgl-10" aria-hidden="true"><use xlink:href="#qsi-right"></use></svg></dd>
      </dl>
    </a>
    <dl class="shop-dl" @click="goWx">
      <dt>微信号</dt>
      <dd>18382069823<svg class="icon mgl-10" aria-hidden="true"><use xlink:href="#qsi-right"></use></svg></dd>
    </dl>

    <transition name="router-sub" mode="out-in">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'shop',
  data () {
    return {
      
    }
  },
  methods: {
    goWx () {
      this.$router.push('/shop/weixin');
    },
    goMap () {
      this.$router.push('/shop/map');
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
.shop-notice{
  height: rem(75);
  line-height: rem(75);
  background-color: #f3f3f3;
  color: #d31176;
  >span{
    color: #d31176;
  }
}

.shop-dl{
  @include flex;
  height: rem(93);
  padding: 0 rem(30);
  border-bottom: rem(1) solid #c2c2c2;
  justify-content: space-between;
  align-items: center;
  font-size: rem(24);
  dt{
    text-align: left;
  }
  dd{
    text-align: right;
  }
}

.shop-head{
  @include flex;
  height: rem(204);
  background-color: #1c78b7;
  align-items: center;
  .img{
    width: rem(120);
    height: rem(120);
    border: 4px solid #fff;
    background: #fff url(../../assets/images/shop-logo.png) center center no-repeat;
    background-size: cover;
    border-radius: 100%;
    overflow: hidden;
  }
  .text{
    @include flex-1;
    text-shadow: 2px 2px 5px #444;
  }
}
</style>



