<template>
  <header class="header-top">
    <slot name="home-header"></slot>  
    <div class="sub-back" v-if="goBack" @click="$router.go(-1)">
      <svg class="icon" aria-hidden="true"><use xlink:href="#qsi-fanhui1"></use></svg>
    </div>
    <h2 class="title fs-28">{{ title }}</h2>
  </header>
</template>

<script>
export default {
  data () {
    return {
      
    }
  },
  props:['goBack', 'title']
}
</script>

<style lang="scss" scoped>
  @import '../assets/css/mixin';
  .header-top{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: rem(75);
    @include flex;
    background-color: #fff;
    box-shadow: 0 rem(2) rem(10) #e6e4e4;
    z-index: 999;
    .title{
      @include flex-1;
      margin-left: rem(75);
      margin-right: rem(75);
      line-height: rem(75);
      text-align: center;
      color: #333;
    }
  }
  .sub-back{
    position: absolute;
    top: 0;
    left: 0;
    width: rem(75);
    line-height: rem(75);
    text-align: center;
    font-size: rem(32);
  }
</style>



