<template>
  <div class="page-sub">
    <qs-header goBack="true" title="店铺地图"></qs-header>
    <section class="page-sub-body">
      <el-amap class="amap-box" vid="amap-vue" :zoom="zoom" :center="center" :plugin="plugin">
        <el-amap-marker vid="component-marker" :position="componentMarker.position"></el-amap-marker>
      </el-amap>
    </section>
  </div>
</template>

<script>
import qsHeader from '../../components/header'

export default {
  name: 'map',
  data () {
    const self = this;
    return {
      zoom: 19,
      center: [103.844980,29.836088],
      componentMarker: {
        position: [103.844980,29.836268]
      },
      icon: '../../assets/pic/1.jpg',
      loaded: false,
      lng: 0,
      lat: 0,
      plugin: [{
        enableHighAccuracy: true,//是否使用高精度定位，默认:true
        timeout: 5000,          //超过10秒后停止定位，默认：无穷大
        maximumAge: 0,           //定位结果缓存0毫秒，默认：0
        convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
        showButton: false,        //显示定位按钮，默认：true
        buttonPosition: 'RB',    //定位按钮停靠位置，默认：'LB'，左下角
        showMarker: true,        //定位成功后在定位到的位置显示点标记，默认：true
        showCircle: true,        //定位成功后用圆圈表示定位精度范围，默认：true
        panToLocation: false,     //定位成功后将定位到的位置作为地图中心点，默认：true
        zoomToAccuracy: true,//定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
        extensions:'all',
        pName: 'Geolocation',
        events: {
          init(o) {
            // o 是高德地图定位插件实例
            // o.getCurrentPosition((status, result) => {
            //   //console.log(result)
            //   if (result && result.position) {
            //     self.lng = result.position.lng;
            //     self.lat = result.position.lat;
            //     self.center = [self.lng, self.lat];
            //     self.loaded = true;
            //     self.$nextTick();
            //   }
            // });
          }
        }
      }]
                // 103.844757,29.837088
                // 103.842350,29.836578
    }
  },
  components: {
    qsHeader
  },
  created () {
    
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
</style>



